import { Component, OnInit } from '@angular/core';
import { OwlOptions } from 'ngx-owl-carousel-o';
import { baners } from '../models/baners';
import { GetReturantDataService } from '../service/get-returant-data.service';
@Component({
  selector: 'app-advertisement',
  templateUrl: './advertisement.component.html',
  styleUrls: ['./advertisement.component.scss'],
})
export class AdvertisementComponent implements OnInit {
  constructor(private _GetRetardantDataService: GetReturantDataService) {}
baners:baners[];
  ngOnInit(): void {
        this._GetRetardantDataService.getBaners().subscribe(
      (data: baners[]) => {
        this.baners = data;
      },
      (err) => {
        console.log(err);
      }
    );
  }
   customOptions: OwlOptions = {
    loop: false,
    nav: false,
    rtl: true,
    mouseDrag: true,
    touchDrag: true,
    pullDrag: true,
    margin:10,
    dots: true,
    navSpeed: 700,
    responsive: {
      0: {
        items: 1,
      },
      300: {
        items: 1,
      },
      500: {
        items: 2,
      },
       600: {
        items: 2,
      },
       700: {
        items: 2,
      },
          800: {
        items: 3,
      },
      1000: {
        items: 3,
      },
    },
  };
}
