import { Component ,EventEmitter ,Output} from '@angular/core';
import { GetReturantDataService } from '../service/get-returant-data.service';
import { OwlOptions } from 'ngx-owl-carousel-o';

import { FoodCategories } from '../models/FoodCategories';


@Component({
  selector: 'app-nav-bar-mobile',
  templateUrl: './nav-bar-mobile.component.html',
  styleUrls: ['./nav-bar-mobile.component.scss'],
})
export class NavBarMobileComponent {
  @Output()  sendMop = new EventEmitter<string>();
  constructor(public _GetReturantDataService: GetReturantDataService) {}
  navBarItems: FoodCategories[];
    arr: Array<string> = [];

  ngOnInit(): void {
    this._GetReturantDataService.GetFoodCategories().subscribe(
      (res) => {
        this.navBarItems = res;
      },
      (err) => {
        console.log(err);
      }
    );
  }
  customOptions: OwlOptions = {
    loop: false,
    nav: true,
    rtl: true,
    navText: ['<i class="fas fa-chevron-right color-arrow menu-right"></i>',
    '<i class="fas fa-chevron-left color-arrow menu-left"></i>'],
    mouseDrag: true,
    touchDrag: true,
    pullDrag: true,
    dots: true,
    navSpeed: 700,
    responsive: {
      0: {
        items: 3,
      },
      300: {
        items: 4,
      },
      500: {
        items: 5,
      },
      700: {
        items: 7,
      },
      900: {
        items: 7,
      },
      1000: {
        items: 8,
      },
    },
  };
Confert:string;
  toCategories:string;
  onCheckboxChange(e) {
    if (e.target.checked) {
      this.arr.push(e.target.value);
      this.Confert=this.arr.toString();
      this.toCategories=this.Confert.replace(/,/g, '');
         this.sendMop.emit(this.toCategories)


    } else {
      let i: number = 0;
      this.arr.forEach((item) => {
        if (item == e.target.value) {
          this.arr.splice(i, 1);
          this.Confert=this.arr.toString();
          this.toCategories=this.Confert.replace(/,/g, '');

         this.sendMop.emit(this.toCategories)

          return;
        }
        i++;
      });
    }

  }
}
