import { Component, OnInit ,Input, EventEmitter, Output } from '@angular/core';
import { RestaurantModel } from '../models/RestaurantModel';
import { GetRestaurantAreas } from '../models/GetRestaurantAreas';
import { GetReturantDataService } from '../service/get-returant-data.service';
@Component({
  selector: 'app-restaurant-contact',
  templateUrl: './restaurant-contact.component.html',
  styleUrls: ['./restaurant-contact.component.scss'],
})
export class RestaurantContactComponent implements OnInit {
  @Input() indexResturant;
  @Input() showContat;
  @Output() showContat2 = new EventEmitter();
  close() {
    this.showContat2.emit((this.showContat = false));
  }
  Restaurants: GetRestaurantAreas[];
  itemRestaurant: any;
  isLodging: boolean = true;
  num: any;
  valueChange: any;

  constructor(private _GetRestaurantDataService: GetReturantDataService) {}
  ngOnInit(): void {}
  clasePopup(event) {
    if (event.target.classList.contains('popup-details')) {
      this.showContat2.emit((this.showContat = false));
    }
  }
  prev: any;
  changeArea(event) {
    if (this.prev != null) {
      document.getElementById(this.prev).style.display = 'none';
      document.getElementById(this.prev +'-map').style.display = 'none';
    }
    this.valueChange = event;
    this.prev = event;
    const x = document.querySelector('.numbers-restranut') as HTMLElement;
    const y = document.querySelector('.map') as HTMLElement;
    x.style.display = 'none';
    y.style.display = 'none';


    document.getElementById(event).style.display = 'block';
    document.getElementById(event +'-map').style.display = 'block';
  }
  ngOnChanges() {
    this.num = this.indexResturant;
    if (this.num == this.indexResturant) {
      this.isLodging = true;
    }
    this._GetRestaurantDataService.getContact(this.indexResturant).subscribe(
      (data: GetRestaurantAreas[]) => {
        this.Restaurants = data;
        this.itemRestaurant = this.Restaurants;
        this.isLodging = false;
      },
      (err) => {
        this.isLodging = true;
      }
    );
  }
}
