import { popupNotification } from './../models/popupNotification';
import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { RestaurantModel  } from '../models/RestaurantModel';
import { GetReturantDataService } from '../service/get-returant-data.service';

@Component({
  selector: 'app-restaurants',
  templateUrl: './restaurants.component.html',
  styleUrls: ['./restaurants.component.scss'],
})
export class RestaurantsComponent implements OnInit {
  pageLoading: boolean = true;
  openNot: boolean = false;
  lodingPopuo: boolean = true;
  popupNotif: boolean = false;
  errorMessage: any;
  popupNotification: popupNotification[];
  resiveChange(e) {
    this.openNot = false;
  }
  openNotfaction() {
    this.openNot = true;
  }
  page: number = 1;

  indexResturant: any;
  showContat: boolean = false;
  Restaurants: RestaurantModel[];
  pagenationArray: any[];
  navSocial: boolean = false;
  lengthData: number;
  isLodging: boolean = true;
  search: any;
  length: any;
  constructor(private _GetRetardantDataService: GetReturantDataService) {}
  showMobileSideNav() {
    if (this.navSocial) {
      this.navSocial = false;
    } else {
      this.navSocial = true;
    }
  }
  ngOnInit(): void {
    this.getAllRe();
    this._GetRetardantDataService
      .popupNotification()
      .subscribe((item: popupNotification[]) => {
        this.popupNotification = item;
        this.lodingPopuo = false;

      });
    this._GetRetardantDataService.Notification().subscribe((item) => {
      this.length = item;
    });
  }
  getAllRe() {
    this._GetRetardantDataService.getAllRestaurant(this.page).subscribe(
      (data: RestaurantModel[]) => {
        this.Restaurants = data;
        this.lengthData = this.Restaurants.length;
        this.isLodging = false;
        this.popupNotif = true;
        
        this.pageLoading=false;
      },
      (err) => {
        console.log(err);
        this.isLodging = true;
        this.errorMessage = err;
                this.pageLoading=true;

      }
    );
  }

  clasePopup(e) {
    this.showContat = false;
  }
  get(i) {
    this.indexResturant = i;
    this.showContat = true;
  }
  claseAll(event) {
    if (event.target.classList.contains('nav-social')) {
      this.navSocial = false;
    }
  }
  claseNotification(event) {
    if (event.target.classList.contains('offers-item')) {
      this.popupNotif = false;
    }
  }
  message: any;
  receiveMessage($event) {
    this.isLodging = true;
    this.message = $event;
    this.lengthData = 1;
    this._GetRetardantDataService
      .getAllRestaurant(null, this.message, this.Features)
      .subscribe(
        (data: RestaurantModel[]) => {
          this.Restaurants = data;
          this.lengthData = this.Restaurants.length;
          this.isLodging = false;
        },
        (err) => {
          console.log(err);
          this.isLodging = true;
        }
      );
  }
  Features: any;
  receiveFeatures(event) {
    this.isLodging = true;
    this.Features = event;
    this.lengthData = 1;

    this._GetRetardantDataService
      .getAllRestaurant(null, this.message, this.Features)
      .subscribe(
        (data: RestaurantModel[]) => {
          this.Restaurants = data;
          this.lengthData = this.Restaurants.length;
          this.isLodging = false;
        },
        (err) => {
          console.log(err);
          this.isLodging = true;
        }
      );
  }
}
