import { Component, OnInit ,HostListener,Input} from '@angular/core';
import { OwlOptions } from 'ngx-owl-carousel-o';
import { RestaurantModel } from '../models/RestaurantModel';
import { GetReturantDataService } from '../service/get-returant-data.service';
export enum KeyCodes {
  LEFT = 37,
  RIGHT = 39,
  close=27,
}
@Component({
  selector: 'app-menu',
  templateUrl: './menu.component.html',
  styleUrls: ['./menu.component.scss']
})
export class MenuComponent implements OnInit {
    @Input() indexResturant;

    constructor(public _GetReturantDataService:GetReturantDataService) { }
    menus:RestaurantModel;
  ngOnInit() {
    this.innerWidth = window.innerWidth;
    this._GetReturantDataService.getMenu(this.indexResturant)
      .subscribe((arg:RestaurantModel) =>
        {this.image = arg.RestaurantMenus;
        }
        );
  }

  customOptions: OwlOptions = {
    loop: false,
    nav: true,
    rtl: true,
    mouseDrag: true,
    touchDrag: true,
    pullDrag: true,
    margin:10,
    navText: ['<i class="fas fa-chevron-right color-arrow arrow-menu-top  arrow-right-menu" id="right-menu"></i>', 
    '<i class="fas fa-chevron-left color-arrow  arrow-left-menu arrow-menu-top" id="left-menu"></i>'],
    dots: true,
    navSpeed: 700,
    responsive: {
      0: {
        items: 1,
      },
      200: {
        items: 1,
      },
        300: {
        items: 1,
      },
        400: {
        items: 2,
      },
        500: {
        items: 3,
      },
      700: {
        items: 3,
      },
         900: {
        items: 3,
      },
      1000: {
        items: 5,
      },
    },
  };
    @HostListener('document:keyup', ['$event'])
  KeyUpEvent(event: KeyboardEvent) {
    if (event.keyCode == KeyCodes.LEFT)
      this.minus();
    if (event.keyCode == KeyCodes.RIGHT)
      this.add();
      if (event.keyCode == KeyCodes.close)
      this.close();
  }
  image:any[]=[];
  openPopup:boolean=false;
  innerWidth: any;


  imagePopup:string;
  currentImage:number=0;
  show(event,x){
    this.openPopup=true;
    this.currentImage=x;
    this.imagePopup=event.target.src;
  }
  minus(){

    this.currentImage--;
    if (this.currentImage < 0) {
        this.currentImage = this.image.length - 1;
        this.imagePopup=this.image[this.currentImage].ImgURl;

    }else{
      this.imagePopup=this.image[this.currentImage].ImgURl;
    }
  }
    add(){

    this.currentImage++;
    if (this.currentImage == this.image.length) {
        this.currentImage = 0;
       this.imagePopup=this.image[0].ImgURl;

    }else{
        this.imagePopup=this.image[this.currentImage].ImgURl;
    }

  }
  close(){
    this.openPopup=false;

  }
popupChangeImage(event){
let clientX=event.clientX;
   if (this.innerWidth < 700) {
        if (clientX < 250) {
  this.minus();
        } else {
            this.add();
        }
    } else if (this.innerWidth > 700) {
        if (clientX < 700) {
  this.minus();
        } else {
            this.add();
        }
    }
}
  clasePopup(event)
{
  if(event.target.classList.contains('menu-Pop'))
  {
this.openPopup=false;
  }
}
}
