import { Component, OnInit } from '@angular/core';
import { GetReturantDataService } from '../service/get-returant-data.service';
import { FoodCategories } from '../models/FoodCategories';
import { RouterModule, Router, NavigationEnd } from '@angular/router';
import { filter, pairwise } from 'rxjs/operators';
import * as $ from 'jquery';
import {Output, EventEmitter} from '@angular/core';

@Component({
  selector: 'app-nav-bar',
  templateUrl: './nav-bar.component.html',
  styleUrls: ['./nav-bar.component.scss'],
})
export class NavBarComponent implements OnInit {
@Output()  messageEvent = new EventEmitter<string>();
  prev: boolean=true;
  navBarItems: FoodCategories[];
  location: string;
    arr: Array<string> = [];

  constructor(
    public _GetRestaurantDataService: GetReturantDataService,
    public router: Router
  ) {
 

     if(this.router.url === '/Restaurant')
    {
        this.prev = true;
    }
    else{
        this.prev = false;
    }
  }

  ngOnInit(): void {
    this._GetRestaurantDataService.GetFoodCategories().subscribe(
      (res) => {
        this.navBarItems = res;
      },
      (err) => {
        console.log(err);
      }
    );
  }
  dataConfert:string;
  add:string;
  onCheckboxChange(e) {
    if (e.target.checked) {
      this.arr.push(e.target.value);
      this.dataConfert=this.arr.toString();
      this.add=this.dataConfert.replace(/,/g, '');
      this.messageEvent.emit(this.add)
    } else {
      let i: number = 0;
      this.arr.forEach((item) => {
        if (item == e.target.value) {
          this.arr.splice(i, 1);
          this.dataConfert=this.arr.toString();
          this.add=this.dataConfert.replace(/,/g, '');
          this.messageEvent.emit(this.add)
          return;
        }
        i++;
      });
    }

  }
}
