import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { OwlOptions } from 'ngx-owl-carousel-o';
import { GetFoodCategories } from '../models/GetFoodCategories';
import { GetReturantDataService } from '../service/get-returant-data.service';

@Component({
  selector: 'app-restaurant-features',
  templateUrl: './restaurant-features.component.html',
  styleUrls: ['./restaurant-features.component.scss'],
})
export class RestaurantFeaturesComponent implements OnInit {
  @Output() sendFeatures = new EventEmitter<string>();

  allCategories: GetFoodCategories[];
  lengthData: number;
  constructor(public _GetReturantDataService: GetReturantDataService) {}

  customOptions: OwlOptions = {
    loop: false,
    nav: false,
    rtl: true,
    mouseDrag: true,
    touchDrag: true,
    pullDrag: true,
    dots: true,
    navSpeed: 700,
    responsive: {
      0: {
        items: 1,
      },
      300: {
        items: 2,
      },
      500: {
        items: 4,
      },
      700: {
        items: 6,
      },
      900: {
        items: 7,
      },
      1000: {
        items: 8,
      },
    },
  };
  ngOnInit(): void {
    this._GetReturantDataService.GetFeatures().subscribe(
      (data) => {
        this.allCategories = data;
      },
      (error) => {
        console.log(error);
      }
    );
  }
  arr: Array<string> = [];
  dataConfert: string;
  add: string;

  onCheckboxChange(e) {
    if (e.target.checked) {
      this.arr.push(e.target.value);
      this.dataConfert = this.arr.toString();
      this.add = this.dataConfert.replace(/,/g, '');
      this.sendFeatures.emit(this.add);
    } else {
      let i: number = 0;
      this.arr.forEach((item) => {
        if (item == e.target.value) {
          this.arr.splice(i, 1);
          this.dataConfert = this.arr.toString();
          this.add = this.dataConfert.replace(/,/g, '');

          this.sendFeatures.emit(this.add);
          return;
        }
        i++;
      });
    }
  }
}
