import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';
//models
import { RestaurantModel } from '../models/RestaurantModel';
import { FoodCategories } from '../models/FoodCategories';
import { GetRestaurantAreas } from '../models/GetRestaurantAreas';
import { GetFoodCategories } from '../models/GetFoodCategories';
import { baners } from '../models/baners';
import { popupNotification } from '../models/popupNotification';
import {Notification } from '../models/Notification';
// end models
import { throwError } from 'rxjs';
import { map, filter, catchError, mergeMap } from 'rxjs/operators';
@Injectable({
  providedIn: 'root',
})
export class GetReturantDataService {
  baseUrl = `https://appservice.banha-restaurants.com/api/Restaurant/v1`;
  urlFeatures=`https://appservice.banha-restaurants.com/api/APP/v1`;
  urlNotifications=`https://appservice.banha-restaurants.com/api/Notification/v1/GetNotifications?`
  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type': 'application/json',
    }),
  };

  constructor(private _HttpClient: HttpClient) {}
  getAllRestaurant(
    number: number = 1,
    order: any = undefined,
    Features: any = undefined
  ): Observable<RestaurantModel[]> {
    if (order !== undefined && Features === undefined) {
      return this._HttpClient.get<RestaurantModel[]>(
        `${this.baseUrl}/GetRestaurants?${order}`,
        this.httpOptions
      );
    } else if (Features !== undefined && order === undefined) {
      return this._HttpClient.get<RestaurantModel[]>(
        `${this.baseUrl}/GetRestaurants?${Features}`,
        this.httpOptions
      );
    } else if (Features !== undefined && order !== undefined) {
      return this._HttpClient.get<RestaurantModel[]>(
        `${this.baseUrl}/GetRestaurants?$${order}${Features}`,
        this.httpOptions
      );
    } else {
      return this._HttpClient.get<RestaurantModel[]>(
        `${this.baseUrl}/GetRestaurants?PageNumber=${number}`,
        this.httpOptions
      );
    }
  }
  GetFoodCategories(): Observable<FoodCategories[]> {
    return this._HttpClient.get<FoodCategories[]>(
      `${this.urlFeatures}/GetFoodCategories`,
      this.httpOptions
    );
  }
  GetFeatures(): Observable<GetFoodCategories[]> {
    return this._HttpClient.get<GetFoodCategories[]>(
      `${this.urlFeatures}/GetFeatures`,
      this.httpOptions
    );
  }
  getMenu(indexRestaurant: any = 1): Observable<RestaurantModel> {
    //
    return this._HttpClient.get<RestaurantModel>(
      `${this.baseUrl}/GetRestaurantProfile?Id=${indexRestaurant}`,
      this.httpOptions
    );
  }
  getContact(indexRestaurant: any = 1): Observable<GetRestaurantAreas[]> {
    return this._HttpClient.get<GetRestaurantAreas[]>(
      `${this.baseUrl}/GetRestaurantAreas?Id=${indexRestaurant}`,
      this.httpOptions
    );
  }
  getBaners(): Observable<baners[]>{
       return this._HttpClient.get<baners[]>(
      `${this.urlNotifications}Fk_NotificationType=5`,
      this.httpOptions
    );
  }

  popupNotification(): Observable<popupNotification[]> {
           return this._HttpClient.get<popupNotification[]>(
      `${this.urlNotifications}Fk_NotificationType=7`,
      this.httpOptions
    );
  }
    Notification(): Observable<Notification[]> {
           return this._HttpClient.get<Notification[]>(
      `${this.urlNotifications}Fk_NotificationType=6`,
      this.httpOptions
    );
  }

}
