import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { RestaurantModel } from '../models/RestaurantModel';
import { GetReturantDataService } from '../service/get-returant-data.service';
@Component({
  selector: 'app-restaurant-details',
  templateUrl: './restaurant-details.component.html',
  styleUrls: ['./restaurant-details.component.scss'],
})
export class RestaurantDetailsComponent implements OnInit {
  pageLoading: boolean = true;

  indexResturant: any;
  pageName: any;
  showContat: boolean = false;
  constructor(
    public _GetReturantDataService: GetReturantDataService,
    public _ActivatedRoute: ActivatedRoute
  ) {
    this.indexResturant = this._ActivatedRoute.snapshot.paramMap.get('id');
    this.pageName = this._ActivatedRoute.snapshot.paramMap.get('name');
  }
  allRestaurant: RestaurantModel;
  dataRestaurant: RestaurantModel;
  ngOnInit(): void {
    if (this.indexResturant !== undefined) {
      this._GetReturantDataService.getMenu(this.indexResturant).subscribe(
        (data) => {
          this.allRestaurant = data;
          this.dataRestaurant = this.allRestaurant;
                  this.pageLoading=false;

        },
        (err) => {
          console.log(err);
                  this.pageLoading=true;

        }
      );
    }
  }
  displaySum() {
    this.showContat = false;
  }
  show(indexResturant) {
    this.showContat = true;
  }
  clasePopup(e) {
    this.showContat = false;
  }
}
